.header {
    margin-top: 40px;
}

.headerTitles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', sans-serif;
    color: #444;
}

.headerTitlesSm {
    position: absolute;
    top: 16%;
    font-size: 20px;
}

.headerTitlesLg {
    position: absolute;
    top: 19%;
    font-size: 100px;
}

.headerImg {
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
}